<template>
  <div id="app" v-cloak>
    <router-view></router-view>
  </div>
</template>

<script>
import { post, get, host, wx } from "@/utils";
export default {
  name: "app",
  methods: {
    resGetUserInfo() {
      let openid = window.localStorage.getItem("openid");
      return get("shop/api/getUserInfo/openid/" + openid);
    },
    resCart() {
      // 
      let sessid = window.localStorage.getItem("PHPSESSID");
      return get("shop/api/cart/PHPSESSID/" + sessid);
    },
    getQueryString (name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i"); 
      var r = window.location.search.substr(1).match(reg); 
      if (r != null) return unescape(r[2]); 
      return null; 
    }
  },
  created() {
    const _this = this;

    let pbid = parseInt(this.getQueryString('pbid')) || 78
    console.log('pbid',pbid);
    //
    let old_pbid=window.localStorage.getItem("pbid");
    if(old_pbid != pbid){
      //
       window.localStorage.clear();
       window.localStorage.setItem("pbid", pbid);
    }
   
    let state = window.localStorage.getItem("openid");
    document.title = window.localStorage.getItem("public_name") || ""
    if (state) {
      console.log("");
      // PHPSESSID
      post('weiapp/Api/checkLogin/', {
        PHPSESSID: window.localStorage.getItem('PHPSESSID')
      }).then((res) => {
        let data = JSON.parse(res)
        console.log('checkLogin', data.msg)
        if (data.status == 0) {
          window.location.href = host + "weixin/wap/vue_login";
        }
      })
      this.$http
        .all([_this.resGetUserInfo(), _this.resCart()])
        .then(
          _this.$http.spread(function(records, projects) {
            //

            // 
            window.localStorage.setItem(
              "userInfo",
              JSON.stringify(records.user_info)
            );

            // 
            let num = projects.lists.length;
            _this.$store.commit("getCartShopNum", {
              num: num
            });
          })
        )
        .catch(function(error) {
          console.log(error);
        });
      
      
    } else {
      
      window.location.href = host + "weixin/wap/vue_login";
    }
  }
};
</script>

