  -moz-osx-font-smoothing: grayscale;
  color: #333;
  font-size: 16px;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
body {
  color: #333;
  background: #f9f9f9;
  font-size: 18px;
}
[v-cloak] {
  display: none;
}
